    margin-top: 0;
  }
}
@media (max-width: 790px) {
      .sitelogo{
        width: 60px;
        height: 60px;
      }
      .burger__menu {
        display: block;
        position: relative;
        width: 42px;
        height: 32px;
        background-color: $blue;
        border-radius: 2px;
        z-index: 1500;
        &:before{
          content: '';
          background-color: white;
          position: absolute;
          width: 50%;
          height: 2px;
          left: 10px;
          top: 10px;
          transition: all 0.3s ease 0s;
        }
        & span{
          position: absolute;
          left: 10px;
          width: 50%;
          background-color: white;
          top: 16px;
          height: 2px;
          transition: all 0.3s ease 0s;
        }
        &:after{
          content: '';
          background-color: white;
          position: absolute;
          width: 50%;
          height: 2px;
          left: 10px;
          top: 22px;
          transition: all 0.3s ease 0s;
        }
      }
      .header__menu{
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: 400px;
        background-color: white;
        transition: all 0.3s ease 0s;
        & ul{
          padding-top: 44px;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
        }
      }
      .burger__menu.active:before{
          transform: rotate(45deg);
          top: 16px;
      }
      .burger__menu.active:after{
        transform: rotate(-45deg);
        top: 16px;
      }
      .burger__menu.active span{
        transform: scale(0);
      }
      .header__menu.active{
        top: 0;
      }
}
